header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  .header {
    display: flex;
    justify-content: space-between;
    padding-top: 90px;

    .menu-block {
      display: flex;
      align-items: center;
      overflow: hidden;

      button:active, button:focus {
        outline: none;
      }

      .main-menu {
        transform: translateX(100%);
        z-index: -1;
        opacity: 0;
        transition: 1s;

        &.is-active {
          transform: translateX(0);
          z-index: 0;
          opacity: 1;
        }

        .menu {
          display: flex;
          margin: 0;
          padding: 0;
          list-style: none;
          font-size: 20px;

          li {
            margin: 0 20px;

            a {
              color: #ffffff;

              &:hover {
                text-decoration: none;
                color: #00fffc;
              }
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 1199px) {}

  @media screen and (max-width: 991px) {}

  @media screen and (max-width: 767px) {
    .header {
      padding-top: 30px;
    }
  }

  @media screen and (max-width: 575px) {
    .header {
      padding-top: 20px;
    }
  }
}